import { postsAPI } from "../api/api";

const ADD_NEWS = "news/ADD-NEWS"
const SET_NEWS = "news/SET-NEWS"
const SET_FETCHING = "news/SET-FETCHING"


let initialState = {
    news: [
    ],
    img: "data:image/png;base64,",
    isFetching: true
}

export const newsReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_NEWS: {
            let newPost = {
                id: state.news.length + 1,
                title: state.PostText,
                img: state.img,
            }
            return {
                ...state,
                news: [newPost, ...state.news],
            };// redux  ,      state   
        }
        case SET_NEWS:
            return {
                ...state,
                news: [...action.posts]
            }
        case SET_FETCHING:
            return {
                ...state,
                isFetching: action.payload
            }
        default:
            return state;
    }
}


export const AddNews = () => ({
    type: ADD_NEWS,
})

export const setPosts = (posts) => ({
    type: SET_NEWS,
    posts
})

const setFetching = (load) => ({
    type: SET_FETCHING,
    payload: load
})


export const getPostsThunkCreator = () => {
    return async (dispatch) => {
        dispatch(setFetching(true));
        const posts = await postsAPI.getPosts();
        dispatch(setPosts(posts))
        dispatch(setFetching(false))
    }
}