import React from 'react'
import classes from "../Users.module.css"
import defaultPhoto from "../../../assets/ava.png"
import { Navigate, NavLink, useNavigate } from 'react-router-dom'
import addFriend from "../../../assets/addFriend.png"

export const PossibleFriends = (props) => {
  let Friends = props.possibleFriends.slice(0, 5);
  const follow = (id) => {
    props.followed(id, true);
    props.filterFriends(id);
  }

  let navigate = useNavigate();


  const profileLink = (id) => {
    navigate("/profile/" + id)
  }

  return (
    <div className={classes.possibleFriends}>
      <div className={classes.headerFriends}>
        <span> </span>
      </div>
      {
        Friends.map(friend => {
          return (
              <div className={classes.friendBlock} key={friend.id}>
                <div className={classes.friend} onClick={() => profileLink(friend.id)}>
                  <div className={classes.titleImg}>
                    <div className={classes.friendImg}>
                      <img src={friend.photos.large || defaultPhoto} />
                    </div>
                    <div className={classes.friendName}><span>{friend.name}</span></div>
                  </div>
                </div>
                <div className={classes.addFriendsBlock}>
                  <div className={classes.addFriend} onClick={() => follow(friend.id)}>
                    <img src={addFriend} />
                  </div>
                </div>
              </div>
          )
        })
      }
    </div >
  )
}
